.form-control {
    width: 550px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: -12px;
}

.form-control:disabled, .form-control[readonly] {
    background-color: inherit;
    opacity: 1;
}

h1 {
    font-size: 2.5rem;
    margin-top: 20px;
}
.header {
    margin-top: 0
}

.select-status {
    color: #666;
}

@media (max-width: 540px) {
    .form-control {
        width: 450px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: -12px;
    }

    .btn {
        margin-left: auto;
    }
}

@media (max-width: 414px) {
    .form-control {
        width: 350px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: -12px;
    }

    .btn {
        margin-left: auto;
    }
}

@media (max-width: 320px) {
    .form-control {
        width: 270px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: -12px;
    }

    .btn {
        margin-left: auto;
    }
}